import React from 'react';
import { Label } from 'core/src/typography';
import { InputLabelProps as CommonInputLabelProps } from 'shared/types/input';
import { FontColor } from 'core/types/common';
import { BaseInputSizeCore } from 'shared/BaseInput/types';

type InputLabelProps = CommonInputLabelProps<BaseInputSizeCore> & { color: FontColor };

const InputLabel: React.FC<InputLabelProps > = ({
  size = 'small',
  htmlFor,
  color,
  text = ''
}): JSX.Element => (
  <Label
    size={ size }
    htmlFor={ htmlFor }
    color={ color }
  >
    { text }
  </Label>
);

const MemoizedInputLabel = React.memo(InputLabel);

export { MemoizedInputLabel };
